.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;

  @include vp-1199 {
    padding: 0 45px;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.container--about,
.container--advantages {
  @include vp-767 {
    padding: 0;
  }
}

.container--section-combined {
  max-width: none;
  padding: 0;
}
