.footer-socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0;
  padding: 0;

  list-style: none;

  @include vp-1199 {
    margin-right: -6px;
  }

  @include vp-767 {
    justify-content: flex-start;
    margin: 0;
  }
}

.footer-socials a {
  margin-left: 11px;

  color: $color-default-white;

  @include vp-767 {
    margin: 0;
    margin-right: 11px;
  }
}
